.users {
    display: grid;
    grid-template-areas:
        "photo personal location"
        "follow status  location"
    ;

}

.user_card {
    border: 1px solid black;
}

.photo {
    grid-area: photo;
    border-radius: 50px;
    width: 70px;
    height: 70px;
    border: 1px solid rgb(157, 157, 214);
}

.follow_btn {
    grid-area: follow;


}

.follow_btn button {
    outline: none;
    padding: 5px 15px;
    border-radius: 10px;
    color: #fff;
    background: linear-gradient(0deg, rgba(2, 66, 143, 1) 87%, rgba(61, 115, 173, 1) 95%, rgba(118, 150, 228, 1) 100%);
}

.name {
    grid-area: personal;
}

.status {
    grid-area: status;
}

.selectedItem {
    font-weight: bold;
}

.pageItem {
    cursor: pointer;
    display: inline-block;
    margin: 0 3px;
}

.loader {
    width: 200px;
    margin: 10% 35%;
}